.summary {
  background: #fbfbfb;
  height: 100vh; }

.summary > .content {
  color: #202020;
  font-family: "Fira Sans", sans-serif;
  position: relative;
  text-align: center;
  z-index: 10;
  top: 50%;
  -webkit-transform: translate(0%, -50%);
          transform: translate(0%, -50%); }

.topHeader {
  background: #4b95bc;
  display: block;
  height: 12.5vh;
  position: absolute;
  top: 0;
  width: 100%; }

.topHeader > .content {
  height: 100%;
  position: relative; }

.topHeader .leftMenu {
  left: 0;
  top: 50%;
  -webkit-transform: translate(0, -50%);
          transform: translate(0, -50%);
  position: absolute; }

.topHeader .leftMenu a {
  color: #fbfbfb;
  font-size: 1.8em;
  padding-left: 10px;
  padding-right: 10px;
  float: left;
  line-height: 1.4em;
  text-align: center;
  text-decoration: none; }

.topHeader .rightMenu {
  right: 0;
  top: 50%;
  -webkit-transform: translate(0, -50%);
          transform: translate(0, -50%);
  position: absolute; }

.topHeader .rightMenu div {
  display: inline; }

.topHeader .rightMenu i {
  color: #fbfbfb;
  font-size: 2em;
  margin-left: 10px;
  margin-left: 10px;
