.Typewriter {
  font-size: 2.8em;
  font-weight: 500;
  line-height: 2em;
  padding-left: 10px;
  padding-right: 10px;
  text-align: left; }

.Typewriter h1 {
  color: #202020;
  display: inline;
  font-family: "Muli", sans-serif;
  font-size: 1.2em;
  font-weight: 600; }

.Typewriter h5 {
  color: #4281a4;
  display: inline;
  font-family: "Muli", sans-serif;
  font-size: 1em; }

.audioAnimation {
  height: 50px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 50px;
  position: relative;
  width: 64px; }

.audioAnimation span {
  width: 10px;
  height: 60px;
  top: 0px;
  position: absolute;
  background: #202020;
  -webkit-animation: audio 0.5s infinite ease;
          animation: audio 0.5s infinite ease; }

.audioAnimation span:first-child {
  left: 0px;
  -webkit-animation-delay: 0.2s;
  animation-delay: 0.2s; }

.audioAnimation span:nth-child(2) {
  left: 18px;
  -webkit-animation-delay: 0.4s;
  animation-delay: 0.4s; }

.audioAnimation span:nth-child(3) {
  left: 36px;
  -webkit-animation-delay: 0.6s;
  animation-delay: 0.6s; }

.audioAnimation span:nth-child(4) {
  left: 54px;
  -webkit-animation-delay: 0.8s;
  animation-delay: 0.8s; }

.audioAnimation span:nth-child(5) {
  left: 72px;
  -webkit-animation-delay: 1s;
  animation-delay: 1s; }

@-webkit-keyframes audio {
  0% {
    height: 25px; }
  30% {
    height: 30px; }
  60% {
    height: 50px; }
  80% {
    height: 35px; }
  100% {
    height: 20px; } }

@keyframes audio {
  0% {
    height: 25px; }
  30% {
    height: 30px; }
  60% {
    height: 50px; }
  80% {
    height: 35px; }
  100% {
    height: 20px; } }

